import React, { Component } from 'react';
import './BrandCard.css';
export default class BrandCard extends Component {
  render() {
    const { image, alt, description, id } = this.props;
    return (
      <div className="brandCardContainer" id={id.toString()}>
        <img src={image} alt={alt} />
        <p className="brandCardText">{description}</p>
      </div>
    );
  }
}
