import React, { Component } from 'react';
import './Tag.css';

export default class Tag extends Component {
  render() {
    const { tag, tagCategory } = this.props;

    let fontColor = '';
    if (tagCategory === 'discount') {
      fontColor = 'var(--color-brand-500)';
    }
    if (tagCategory === 'new') {
      fontColor = 'var(--color-brand-50)';
    }
    if (tagCategory === 'season') {
      fontColor = 'var(--color-brand-700)';
    }

    let bgColor = '';
    if (tagCategory === 'discount') {
      bgColor = 'var(--color-brand-200)';
    }
    if (tagCategory === 'new') {
      bgColor = 'var(--color-brand-300)';
    }
    if (tagCategory === 'season') {
      bgColor = 'var(--color-brand-50)';
    }

    let border = tagCategory === 'season' ? '1px solid var(--color-brand-700)' : '';

    return (
      <div className="tagContainer" style={{ backgroundColor: bgColor, border }}>
        <div style={{ color: fontColor }}>{tag}</div>
      </div>
    );
  }
}
