import React, { Component } from 'react';
import { Heart } from 'react-feather';
import BuyButton from '../BuyButton';
import Icon from '../Icon';
import Tag from '../Tag/Tag';
import './ProductCard.css';

export default class ProductCard extends Component {
  constructor(props) {
    super(props);
    this.state = {
      favorited: '',
    };
  }

  handleHeartClick = (/* {target} */) => {
    /* const {id} = target; */ // criar redux para armazenar favoritos
    const { favorited } = this.state;
    if (favorited === '') {
      this.setState({ favorited: 'var(--color-heart-50)' });
    } else {
      this.setState({ favorited: '' });
    }
  };

  render() {
    const { image, alt, description, id, price, installments, tag, tagCategory } =
      this.props;
    const { favorited } = this.state;
    return (
      <div className="productCardContainer" id={id.toString()}>
        <Tag tag={tag} tagCategory={tagCategory} />
        <div className="heartContainer" onClick={this.handleHeartClick}>
          <Icon icon={Heart} color="var(--color-heart-50)" width="24" fill={favorited} />
        </div>
        <div className="imgContainer">
          <img src={image} alt={description} />
        </div>
        <p className="productCardDescription">{alt}</p>
        <div className="priceContainer">{price}</div>
        <p>{installments}</p>
        <BuyButton id={id.toString()} />
      </div>
    );
  }
}
