import React, { Component } from 'react';

export default class Icon extends Component {
  render() {
    const { icon: Icon, src, size, width, height, alt, color, fill } = this.props;
    return (
      <>
        {src && (
          <img
            src={src}
            alt={alt}
            width={width || size}
            height={height}
            style={{ stroke: color, fill }}
          />
        )}
        {Icon && <Icon style={{ width, heigth: width, stroke: color, fill }} />}
      </>
    );
  }
}
