import React, { Component } from 'react';
import { modelData } from '../services';
import BrandsContext from './BrandsContext';

class BrandsProvider extends Component {
  constructor() {
    super();
    this.state = {
      modelData: [],
    };
  }

  requestModelData = () => {
    this.setState({
      modelData,
    });
  };

  render() {
    const { Provider } = BrandsContext;
    const { children } = this.props;
    return (
      <Provider value={{ ...this.state, requestModelData: this.requestModelData }}>
        {children}
      </Provider>
    );
  }
}

export default BrandsProvider;
