import React, { Component } from 'react';
import ProductsContext from '../../context/ProductsContext';
import './BuyButton.css';

class BuyButton extends Component {
  handleClick = ({ target }) => {
    const { addShoppingCart } = this.context;
    const { prodData } = this.context;
    const { id } = target;
    addShoppingCart(prodData[id - 1]);
  };
  render() {
    const { id } = this.props;
    return (
      <div className="buyButtonContainer" id={id.toString()} onClick={this.handleClick}>
        <button type="button" id={id.toString()}>
          comprar
        </button>
      </div>
    );
  }
}

BuyButton.contextType = ProductsContext;
export default BuyButton;
