import React, { Component } from 'react';
import { prodData } from '../services';
import ProductsContext from './ProductsContext';

class ProductsProvider extends Component {
  constructor() {
    super();
    this.state = {
      prodData: [],
      shoppingCart: [],
    };
  }

  requestProductsData = () => {
    this.setState({
      prodData,
    });
  };

  addShoppingCart = (newProduct) => {
    this.setState({
      shoppingCart: [...this.state.shoppingCart, newProduct],
    });
  };

  render() {
    const { Provider } = ProductsContext;
    const { children } = this.props;
    return (
      <Provider
        value={{
          ...this.state,
          requestProductsData: this.requestProductsData,
          addShoppingCart: this.addShoppingCart,
        }}
      >
        {children}
      </Provider>
    );
  }
}

export default ProductsProvider;
