import React, { Component } from 'react';
import Slider from 'react-slick';
import ProductsContext from '../../context/ProductsContext';
import ProductCard from '../ProductCard';
import './ProductCarousel.css';

class ProductCarousel extends Component {
  componentDidMount() {
    const { requestProductsData } = this.context;
    requestProductsData();
  }
  render() {
    const { prodData } = this.context;

    var settings = {
      infinite: false,
      speed: 500,
      slidesToShow: 4,
      slidesToScroll: 4,
      initialSlide: 0,
      responsive: [
        {
          breakpoint: 1400,
          settings: {
            slidesToShow: 3,
            slidesToScroll: 3,
            infinite: false,
            dots: true,
          },
        },
        {
          breakpoint: 900,
          settings: {
            slidesToShow: 1.5,
            slidesToScroll: 1,
            infinite: false,
            nextArrow: false,
            prevArrow: false,
            dots: true,
          },
        },
      ],
    };
    return (
      <div className="productCarousel">
        <h2> Destaques </h2>
        <Slider {...settings}>
          {prodData.length > 0 &&
            prodData.map((e) => (
              <ProductCard
                key={e.id}
                image={e.image}
                alt={e.description}
                description={e.description}
                id={e.id}
                price={e.price}
                installments={e.installments}
                tag={e.tag}
                tagCategory={e.tagCategory}
              />
            ))}
        </Slider>
      </div>
    );
  }
}

ProductCarousel.contextType = ProductsContext;
export default ProductCarousel;
